body {
    background: rgb(0, 148, 249);
    text-align: center;
}

#profile_image {
    margin-top: 20vh;
    text-align: center;
}

#profile_image img {
    border: 5px solid rgb(1, 56, 137);
    border-radius: 15px;
    width: 150px;
    height: 150px;
}

section {
    background: white;
    text-align: center;
    padding: 1em;
    width: calc(768px - 2em);
    display: inline-block;
    border-radius: 15px;
    margin-top: 1em;
}